import { GET_ERRORS,REMOVE_ERRORS } from '../actions/types';

const initialState = {};

export default function(state = initialState, action ) {
    switch(action.type) {
        case GET_ERRORS:
            return action.payload;
        case REMOVE_ERRORS:
            return action.payload;
        default:
            return state;
    }
}



